
.header
{
  background: #fff;

  position: fixed;
  width: 100vw;
  z-index: 8;
  bottom:0;
  font-size: 0;
}
.header>div
{
  width:25%;
  display: inline-block;
  text-align:center;
  font-size: 12px;
  /*color:#47CED4;*/
  color:#333;
  padding-bottom: 10px;
}
.header>div img
{
  padding-top: 10px;
  padding-bottom: 4px;
  
  width:20px;
}



